* {
    margin: 0;
    padding: 0;
}

.wrapper_footer {
    background-color: #211F20;
}

.wrapper_footer .footer h3 {
    color: #CFCFCF;
}

.socialIcons {
    border: 1px solid;
    width: 250px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}

.socialIcon {
    width: 50px;
    height: 50px;
    background-color: aqua;
}

.footer {
    flex-direction: column;
    justify-content: flex-start;
    /*display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;*/
}

footer {
    display: flex;
    justify-content: center;
    color: #CFCFCF;
}