.skillsBlock {
    min-height: 100vh;
    background-color: aquamarine;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;

}

.title {
    background-color: darkgray;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px;
}

@media screen and (max-width: 680px){
    .skills {
        justify-content: center;
    }
}